<template>
  <div class="commonHeader">
    <v-app-bar app>
      <div class="routerBox">
        <a @click="onClickGoBack" class="goBack"></a>
        <button type="button" class="goBack layer" @click="onClickGoBack">
           
        </button>
        <a @click="onClickHome" class="goHome"></a>
      </div>
      <v-toolbar-title class="textBlue">{{ title }}</v-toolbar-title>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon
      ><!--    -->
    </v-app-bar>

    <!-- S: LnB  -->
    <v-navigation-drawer v-model="drawer" fixed temporary>
    </v-navigation-drawer>
    <!-- E: LnB  -->
  </div>
</template>

<script>
export default {
  props: {
    title: String,
    routerPath: {
      default: '',
      type: String,
      required: false,
    }
  },
  data () {
    return {
      drawer: null,
    };
  },
  methods: {
    /**
     *  
     * 
     * @method
     */
    onClickHome () {
      let homeRouterPath = this.$route.path.startsWith('/fc') ? '/fc/feedMain' : '/user/feedMain'

      this.$router.push({ path: homeRouterPath })
    },

    /**
     *  
     * 
     * @method onClickGoBack
     */
    onClickGoBack () {
      if (this.title === ' ' || this.title === ' ' || this.title === '   ') {
        this.$emit('clickGoBack')
      } else if (this.routerPath.length !== 0) {
        this.$router.push({ path: this.routerPath })
      } else {
        this.$router.go(-1)
      }
    }
  },
};
</script>