<template>
  <v-app>
    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<style lang="scss">
@import "./src/assets/scss/app";
</style>

<script>
import shareEvent from "./shareEvent";
export default {
  name: "App",
  data: () => ({}),
  created() {
    window.getApp = this;

    //   
    shareEvent.forEach((item) => {
      this.$on(item.name, item.callback);
    });

    //        true,  false  
    const isEndWithConsonant = (korStr) => {
      const finalChrCode = korStr.charCodeAt(korStr.length - 1);
      // 0 =  ,   =  
      const finalConsonantCode = (finalChrCode - 44032) % 28;
      return finalConsonantCode !== 0;
    };
    // / 
    String.prototype.appendEulReul = function () {
      return this + (isEndWithConsonant(this) ? "" : "");
    };
  },
  mounted() {
    document.querySelector("#vuetify-theme-stylesheet").remove();
  },
};
</script>
