/**
 *     0  
 * 
 * @param {Number} strSize   
 * @param {String} str 0  
 * @return {String} result 0  
 */
const fillZero = (strSize, str) => {
  str = str.toString()

  return str.length >= strSize ? str : new Array(strSize - str.length + 1).join('0') + str;//  0 
}
export default { fillZero }