/*
    vue-clipboard2  kakaoPlugin   
    Vue   window.getApp    .
*/
export default [
    {
        name: 'SHARE_EVENT_COPY_URL',
        callback: function (data) {
            if (!data.url) { console.error("url  ."); return; }

            window.getApp.$copyText(data.url).then(function (e) {
                console.log(e)
            }, function (e) {
                console.log(e)
            });
            if (data.callback) {
                data.callback();
            }
        }
    },
    {
        name: 'SHARE_EVENT_SEND_KAKAO',
        callback: function (data) {
            if (!data.title) { console.error("title  ."); return; }
            if (!data.desc) { console.error("desc  ."); return; }
            if (!data.url) { console.error("url  ."); return; }
            window.getApp.$kakao.Link.sendCustom({
                templateId: 58682,
                templateArgs: {
                    thumb: "https://www.affde.com/uploads/article/18271/PYoJ8l12ofd8wJZp.jpg",
                    contentTitle: data.title,
                    contentDesc: data.desc,
                    btnTitle: " ",
                    path: data.url //   
                }
            })
            if (data.callback) {
                data.callback();
            }
        }
    },
];
