import Vue from 'vue'
import VueRouter from 'vue-router'
import StorageUtils from '@/utils/storage-utils'

//    
import UserOnboard from '../view/user/login/Onboard.vue'             //  -  
import UserLogin from '../view/user/login/Login.vue'                 //  -  
import UserAccount from '../view/user/login/Account.vue'             //  -   
import UserProfileSelect from '../view/user/login/ProfileSelect.vue' //  -   

// ,  
import PersonaAnalysis from '../view/personaAnalysis/PersonaAnalysis.vue'               //  -   

// 
import UserFeedMain from '../view/user/feed/FeedMain.vue'                           //  -   

//  
import UserMyPMain from '../view/user/myPage/MyPage.vue'         //  -   
import UserMyPDetail from '../view/user/myPage/MyPageDetail.vue' //  -   
import UserMyPDetailFindFC from '../view/user/myPage/MyPageDetail.vue' //  -   

// 
import UserScheduleMake from '../view/user/schedule/ScheduleMake.vue'       //  -   
import UserScheduleChange from '../view/user/schedule/ScheduleMake.vue'       //  -   
import UserScheduleInquiry from '../view/user/schedule/ScheduleInquiry.vue' //  -    ????
import UserScheduleList from '../view/user/schedule/ScheduleList.vue'       //  -    ????
import UserScheduleCancel from '../view/user/schedule/ScheduleCancel.vue'   //  -   

//  
import UserMatchingFC from '../view/user/findFC/MatchingDone.vue'    //  -    
import UserFcInformation from '../view/user/findFC/FcInfomation.vue' //  -       
import UserMatchingList from '../view/user/findFC/MatchingList.vue'  //  -    
import UserRecommendFC from '../view/user/findFC/RecommendFC.vue'    //  -    


////////////////////////////////////////////
// FC
////////////////////////////////////////////


//    
import FcOnboard from '../view/fc/login/Onboard.vue' //  -  
import FcLogin from '../view/fc/login/Login.vue'     //  -  
import FcAccount from '../view/fc/login/Account.vue' //  -  

// ()
import FcFeedMain from '../view/fc/feed/FeedMain.vue'       //  -   

//  
import FcMypMain from '../view/fc/myPage/MyPage.vue'         //  -   
import FcMypDetail from '../view/fc/myPage/MyPageDetail.vue' //  -   

// 
import FcScheduleMain from '../view/fc/schedule/ScheduleMain.vue'                       //  -   
import FcScheduleDetail from '../view/fc/schedule/ScheduleDetail.vue'                   //  -   
import FcScheduleSetting from '../view/fc/schedule/ScheduleSetting.vue'                 //  -     
import FcScheduleUserPersonaInfo from '../view/fc/schedule/ScheduleUserPersonaInfo.vue' //  -    

//  
import FcSalesLibrary from '../components/fc/proposal/SalesLibrary.vue'

//    
import ShareContentViewer from '../components/include/content/ShareContentViewer.vue'


Vue.use(VueRouter)

const routes = [
  //    
  {
    path: '/',
    redirect: '/user'
  },
  {
    path: '/user',
    name: 'UserOnboard',
    component: UserOnboard
  },
  {
    path: '/user/login',
    name: 'UserLogin',
    component: UserLogin
  },
  {
    path: '/user/account',
    name: 'UserAccount',
    component: UserAccount
  },
  {
    path: '/user/profile',
    name: 'UserProfileSelect',
    component: UserProfileSelect
  },

  // 
  {
    path: '/user/personaAnalysis',
    name: 'UserPersonaAnalysis',
    component: PersonaAnalysis,
    props: (route) => ({
      startPage: route.query.startPage,
      personaLv01: isNaN(parseInt(route.query.personaLv01)) ? -1 : parseInt(route.query.personaLv01),
      userType: "user"
    })
  },

  // 
  {
    path: '/user/feedMain',
    name: 'UserFeedMain',
    component: UserFeedMain
  },

  //  
  {
    path: '/user/myPage',
    name: 'UserMyPMain',
    component: UserMyPMain
  },
  {
    path: '/user/mypDetail',
    name: 'UserMyPDetail',
    component: UserMyPDetail,
    props: true
  },
  {
    path: '/user/mypDetail/:isFindFC',
    name: 'UserMyPDetailFindFC',
    component: UserMyPDetailFindFC,
    props: (route) => ({
      isFindFC: route.params.isFindFC === 'true'
    })
  },
  // 
  {
    path: '/user/scheduleMake',
    name: 'UserScheduleMake',
    component: UserScheduleMake
  },
  {
    path: '/user/scheduleMake/:consultKey',
    name: 'UserScheduleChange',
    component: UserScheduleChange,
    props: (route) => {
      const consultKey = Number.parseInt(route.params.consultKey)
      return Number.isNaN(consultKey) ? -1 : { consultKey };
    }
  },
  {
    path: '/user/scheduleInquiry',
    name: 'UserScheduleInquiry',
    component: UserScheduleInquiry
  },
  {
    path: '/user/scheduleList/:consultKey/:consultState',
    name: 'UserScheduleList',
    component: UserScheduleList,
    props: (route) => ({
      consultKey: isNaN(parseInt(route.params.consultKey)) ? -1 : parseInt(route.params.consultKey),
      consultState: isNaN(parseInt(route.params.consultState)) ? -1 : parseInt(route.params.consultState),
      isLastReservation: route.query.isLastReservation === 'true'
    })
  },
  {
    path: '/user/scheduleCancel/:consultKey',
    name: 'UserScheduleCancel',
    component: UserScheduleCancel,
    props: (route) => {
      const consultKey = Number.parseInt(route.params.consultKey)
      return Number.isNaN(consultKey) ? -1 : { consultKey };
    }
  },
  //  
  {
    path: '/user/matchingFC',
    name: 'UserMatchingFC',
    component: UserMatchingFC
  },
  {
    path: '/user/fcInformation/:fcUserKey',
    name: 'UserFcInformation',
    component: UserFcInformation,
    props: true
  },
  {
    path: '/user/matchingList',
    name: 'UserMatchingList',
    component: UserMatchingList
  },
  {
    path: '/user/recommendFC',
    name: 'UserRecommendFC',
    component: UserRecommendFC
  },

  //////////////////////////////////////////////////////////
  // FC 
  //////////////////////////////////////////////////////////

  // FC>> 
  {
    path: "/fc",
    name: "FcOnboard",
    component: FcOnboard
  },
  {
    path: "/fc/login",
    name: "FcLogin",
    component: FcLogin
  },
  {
    path: "/fc/account",
    name: "FcAccount",
    component: FcAccount
  },

  // FC>> 
  {
    path: '/fc/personaAnalysis',
    name: 'FcPersonaAnalysis',
    component: PersonaAnalysis,
    props: (route) => ({
      startPage: route.query.startPage,
      personaLv01: isNaN(parseInt(route.query.personaLv01)) ? -1 : parseInt(route.query.personaLv01),
      userType: "fc"
    })
  },
  // FC>> 
  {
    path: "/fc/feedMain",
    name: "FcFeedMain",
    component: FcFeedMain,
    props: true
  },

  // FC>>   
  {
    path: "/fc/myPage",
    name: "FcMypMain",
    component: FcMypMain
  },
  {
    path: "/fc/myPageDetail",
    name: "FcMypDetail",
    component: FcMypDetail
  },

  // FC>> 
  {
    path: "/fc/scheduleSetting",
    name: "FcScheduleSetting",
    component: FcScheduleSetting
  },
  {
    path: "/fc/scheduleMain",
    name: "FcScheduleMain",
    component: FcScheduleMain
  },
  {
    path: "/fc/scheduleDetail/:consultingKey",
    name: "FcScheduleDetail",
    component: FcScheduleDetail,
    props: true
  },
  {
    path: "/fc/scheduleUserPersonaInfo/:fromUserKey",
    name: "FcScheduleUserPersonaInfo",
    component: FcScheduleUserPersonaInfo,
    props: true
  },

  // FC>>  
  {
    path: "/fc/salesLibrary",
    name: "FcSalesLibrary",
    component: FcSalesLibrary
  },

  {
    path: "/shareContentViewer/:contentKey",
    name: "ShareContentViewer",
    component: ShareContentViewer,
    props: (route) => {
      const contentKey = Number.parseInt(route.params.contentKey)
      return Number.isNaN(contentKey) ? 0 : { contentKey };
    }
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  StorageUtils.savePreRouterName(from.name === null ? '' : from.name)
  next()
})

export default router